
import React from "react";
import { Button } from "./ui/button";
import { DndContext, useDraggable } from "@dnd-kit/core";
import { SortableContext } from "@dnd-kit/sortable";


interface DataVisualizeProps {
  file: File;
  columns: string[];
  onDelete: () => void;
}

const DataVisualize: React.FC<DataVisualizeProps> = ({ file, columns, onDelete }) => {
  return (
    <div className="flex gap-3 w-full bg-gray-800 p-2 shadow-lg h-full">
      {/* Left side: Scrollable column list */}
      <div className="w-1/5 bg-gray-700 p-4  rounded shadow-lg overflow-y-auto">
        <h2 className="text-xl font-semibold text-white text-center ">{file.name}<br></br> Columns</h2>
        <div className="mt-4 text-white items-center justify-center flex flex-col">
          <DndContext collisionDetection={closestCorners}>
            <SortableContext items={columns}>
              {columns.map((element, index) => (
              <p key={index} className=" border border-gray-900 rounded-md  w-full mt-2 px-4 dark:hover:bg-gray-600 py-2">{element}</p>
              ))} 
            </SortableContext>
    
          </DndContext>
          
        </div>
        <Button
          className="mt-4 px-6 py-2 dark:bg-gray-500 dark:hover:bg-gray-800 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-all w-full"
          onClick={onDelete}
        >
          Upload Another File
        </Button>
        </div>
      {/* Right side: Placeholder for Charts */}
      <div className="w-4/5 bg-gray-900 p-6 rounded shadow-lg flex items-center justify-center">
        <div className="grid grid-cols-2 gap-4 w-full h-full">
          <div className="bg-gray-800 p-4 rounded shadow-lg flex items-center justify-center w-full h-full">
        <h3 className="text-white">Chart 1</h3>
          </div>
          <div className="bg-gray-800 p-4 rounded shadow-lg flex items-center justify-center w-full h-full">
        <h3 className="text-white">Chart 2</h3>
          </div>
          <div className="bg-gray-800 p-4 rounded shadow-lg flex items-center justify-center w-full h-full">
        <h3 className="text-white">Chart 3</h3>
          </div>
          <div className="bg-gray-800 p-4 rounded shadow-lg flex items-center justify-center w-full h-full">
        <h3 className="text-white">Chart 4</h3>
          </div>
        </div>

</div>
</div>
  );
};


export default DataVisualize;
